/* src/Home.css */

.home-container {
    background-color: #f0f4f8; /* Pleasant lighter background color */
    padding: 3rem 1rem; /* Add some padding around */
    text-align: center; /* Center-align text */
}

.profile-section {
    display: flex;
    flex-direction: column;
    align-items: center; /* Center-align items horizontally */
    margin-bottom: 3rem; /* Space below the profile picture section */
}

.profile-picture {
    width: 800px; /* Initial size of profile picture */
    height: 800px;
    border: 5px solid black; /* Black border around the profile picture */
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.5); /* Glow effect */
    transition: transform 0.3s ease;
}

    .profile-picture:hover {
        transform: scale(1.1); /* Enlarge profile picture on hover */
    }

.welcome-text {
    margin: 2rem 0;
    font-size: 3rem; /* Larger font size for better visibility */
    color: black; /* Stylish black color */
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7); /* Stylish shadow effect */
}

.key-highlights {
    background-color: white; /* Background for key highlights */
    border-radius: 8px; /* Rounded corners */
    padding: 2rem; /* Padding inside the box */
    margin: 0 auto; /* Center-align the box horizontally */
    width: 80%; /* Adjust width as needed */
    max-width: 800px; /* Maximum width for better readability */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Subtle shadow for box */
    text-align: left; /* Left-align text inside the box */
}

.key-highlights-heading {
    font-size: 2.5rem; /* Larger font size for heading */
    margin-bottom: 1rem;
    color: #333; /* Dark color for heading */
    text-align: center;
}

.key-highlights-list {
    list-style-type: disc; /* Bullet points */
    margin-left: 20px; /* Space from left */
    font-size: 1.2rem;
}

    .key-highlights-list li {
        margin-bottom: 0.5rem;
        transition: transform 0.3s ease;
    }

        .key-highlights-list li:hover {
            transform: scale(1.05); /* Enlarge list item on hover */
        }
