/* src/Education.css */

.education-container {
    background-color: #f9f9f9; /* Light background color */
    padding: 2rem; /* Padding around content */
}

.education-heading {
    font-size: 2.5rem; /* Larger font size for heading */
    color: #333; /* Dark color for heading */
    margin-bottom: 2rem;
    text-align: center; /* Center align heading */
}

.education-item {
    background-color: white; /* White background for each item */
    padding: 1rem; /* Padding inside each item */
    margin-bottom: 1rem; /* Space between items */
    border-radius: 8px; /* Rounded corners */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Subtle shadow */
}

.education-degree {
    font-size: 1.5rem; /* Font size for degree */
    margin-bottom: 0.5rem; /* Space below degree */
}

.education-institution {
    font-size: 1.2rem; /* Font size for institution */
    color: #555; /* Darker color for institution */
    margin-bottom: 0.5rem;
}

.education-duration {
    font-size: 1rem; /* Font size for duration */
    color: #777; /* Lighter color for duration */
    margin-bottom: 0.5rem;
}

.education-details {
    font-size: 1rem; /* Font size for details */
    color: #444; /* Darker color for details */
}
