.slideshow-section {
    text-align: center;
}

.slideshow-container {
    position: relative;
    max-width: 100%;
    margin: auto;
}

.mySlides {
    display: none;
}

.slide-image {
    width: 100%;
    height: auto;
}

.achievements-list-section {
    padding: 20px;
}

.achievements-heading {
    text-align: center;
    font-size: 2em;
    margin-bottom: 20px;
}

.achievements-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
}

.achievement-box {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 20px;
    width: 300px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}
