/* src/About.css */

.about-container {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 15px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    animation: fadeIn 1s ease-in-out;
}

.about-heading {
    padding: 20px;
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: #333;
}

.about-description {
    font-size: 1.1rem;
    color: #555;
    line-height: 1.8;
    text-align: justify; /* Justify the text */
    margin-bottom: 30px;
}

.skills-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px; /* Space between buttons */
    justify-content: space-between;
    margin-top: 30px;
}

.skills-heading {
    text-align: center; /* Center align the "SKILLS" heading */
    width: 100%; /* Make heading take full width */
    font-size: 2rem; /* Adjust font size for heading */
    margin-bottom: 20px; /* Add some margin below the heading */
    color: #333; /* Color for consistency */
}

.skill-button {
    flex: 0 0 48%; /* Fix width to 48% for consistent two-column layout */
    padding: 8px 10px; /* Smaller size for buttons */
    height: 40px; /* Fixed height for alignment consistency */
    margin: 5px 0; /* Margin between buttons */
    border: none;
    border-radius: 10px; /* Rounded edges */
    background-color: #1976d2;
    color: #fff;
    font-size: 0.9rem; /* Smaller font size */
    cursor: pointer;
    transition: all 0.3s ease;
    text-align: center; /* Center the text within the button */
    box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.2); /* Shadow for projected look */
    display: flex; /* To center text both horizontally and vertically */
    align-items: center;
    justify-content: center;
}

    .skill-button:hover {
        background-color: #145a86;
        transform: translateY(-4px); /* Lift effect on hover */
        box-shadow: 5px 5px 12px rgba(0, 0, 0, 0.3); /* Enhanced shadow on hover */
    }

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}
.social-icons-container {
    display: flex;
    justify-content: center; /* Center the icons */
    gap: 30px; /* Increase space between icons */
    margin: 30px 0; /* Margin around the container */
}

.social-icon {
    font-size: 3rem; /* Increase the size of the icons */
    color: #ffffff; /* White color for icons */
    background-color: #0077b5; /* Background color for icons */
    padding: 15px; /* Padding around the icons */
    border-radius: 50%; /* Rounded circular icons */
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3); /* Deep shadow for projection */
    transition: color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease; /* Smooth transitions */
    text-decoration: none; /* Remove underline */
    display: flex; /* Center icon inside the circle */
    align-items: center; /* Align icon vertically */
    justify-content: center; /* Align icon horizontally */
}

    .social-icon:hover {
        color: #0077b5; /* Change icon color on hover */
        background-color: #ffffff; /* Change background color on hover */
        transform: translateY(-10px); /* Lift the icon up slightly */
        box-shadow: 0 20px 30px rgba(0, 0, 0, 0.5); /* Stronger shadow on hover */
    }

