/* src/Contact.css */
.contact-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 80vh;
    background-color: #f5f5f5;
    padding: 20px;
    animation: fadeIn 1.5s ease-in;
}

.contact-paper {
    padding: 40px;
    background-color: #ffffff;
    border-radius: 15px;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
    animation: slideUp 0.8s ease-out;
    max-width: 800px; /* Increased max-width */
    width: 100%;
    text-align: center;
}

.contact-heading {
    font-size: 2.5rem;
    color: #333;
    font-weight: bold;
    margin-bottom: 20px;
    animation: fadeIn 2s ease-in;
}

.contact-description-container {
    margin: 2rem 0; /* Space above and below the description */
}

.contact-description {
    font-size: 1.1rem;
    color: #555;
}

.contact-details {
    margin-top: 20px;
}

.contact-button {
    font-size: 1rem;
    text-transform: none;
    border-radius: 50px;
    transition: all 0.3s ease;
    width: 100%;
    padding: 10px 20px;
}

    .contact-button:hover {
        background-color: #333;
        color: #fff;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
        transform: scale(1.05);
    }

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes slideUp {
    from {
        transform: translateY(30px);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}
