.work-experience {
    padding: 20px;
    background-color: #f5f5f5;
    color: #333;
}

    .work-experience h1 {
        text-align: center;
        margin-bottom: 20px;
        color: #333;
    }

.experience {
    margin-bottom: 30px;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #fff;
}

    .experience h2 {
        margin: 0 0 10px;
        color: #333;
    }

    .experience h3 {
        margin: 0 0 10px;
        color: #666;
    }

    .experience p {
        margin: 0 0 20px;
        color: #777;
    }

    .experience ul {
        list-style-type: disc;
        padding-left: 20px;
    }

    .experience li {
        margin-bottom: 10px;
        transition: transform 0.3s, font-size 0.3s;
    }

.hover-effect:hover {
    transform: scale(1.05);
    font-size: 1.1em;
}
