.projects {
    padding: 20px;
}

h1 {
    text-align: center;
    margin-bottom: 30px;
    font-size: 2.5rem; /* Larger font size for heading */
    color: #333; /* Dark color for heading */
}

.project-box {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 20px;
    margin: 15px 0;
    position: relative;
}

.project-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.2rem;
}

.github-link {
    color: #333;
    text-decoration: none;
}

.github-icon {
    font-size: 1.5rem;
}

.project-summary {
    margin-top: 10px;
}

    .project-summary p {
        font-size: 1rem;
        margin: 0;
    }

.project-details {
    list-style-type: disc;
    padding-left: 20px;
    margin: 10px 0;
}

.project-box:hover .project-summary {
    display: block;
}

.project-summary:not(.show) {
    display: none;
}
